let urlnya='https://gis.officialapp.my.id'
let sdb = new autophp()
export default {
  data() {
    return {
      datanya: '',
      pilih: '',
      visible: 5,
      kategoris:[],
      temp1: 0,
      temp2: 10,
      pagination: 1,
      pembagiannya: 10,
      orderBy: '',
      orderWith: true,
      searchItem: '',
      kategori: ['','WEB','ANDROID/IOS','IOT','DESKTOP','PWA','SIDE-PROJECT','AUTOMATION','OTHER',]
    }
  },
  layout: 'a-3',
  computed: {
    jumlahPagination () {
      let data = this.datanya
      return data.length / 10
    },
    tombolPagination () {
      let data = this.pagination
      // v-if != (current > max ) button nya
      let btn1 = data - 2
      let btn2 = data - 1
      let btn3 = data
      let btn4 = data + 1
      let btn5 = data + 2
      let array = [btn1, btn2, btn3, btn4, btn5]
      if (array.indexOf(-1) != -1) {
        array = [1, 2, 3, 4, 5]
      } else if (array.indexOf(0) != -1) {
        array = [1, 2, 3, 4, 5]
      }
      return array
    },
    filteredData () {
      let that = this
      let data = this.datanya
      //! order fungsi
      if (this.orderBy.length > 0) {
        data = this.$_.orderBy(data, ['title'], [this.orderBy])
      }
      //! search fungsi
      if (data[0] != undefined) {
        let keys = Object.keys(data[0])
        data = data.filter((v, i, a) => {
          let hasil = false
          keys.filter((key) => {
            if (typeof v[key] === 'string') {
              if (
                v[key].toLowerCase().indexOf(this.searchItem.toLowerCase()) !=
                -1
              ) {
                hasil = true
              }
            } else {
              if (v[key] != undefined) {
                if (v[key].toString().indexOf(this.searchItem) != -1) {
                  hasil = true
                }
              }
            }
          })
          if (hasil) return v
        })
        // data = data.slice(this.temp1, this.temp2)
        data = data.filter((v, i, a) => {
          let hasil = false
          console.log(v.type == that.pilih)
          if (v['type'] == this.pilih) {
            return v
          } else if (this.pilih == '') {
            return v
          }
        })
      }
      // data = data.map(e => {
      //   let obj = {};
      //   let kunci = Object.keys(e);
      //   kunci = that.$_.difference(kunci, that.unless);
