      //         : new Date(e[a]) !== "Invalid Date" && !isNaN(new Date(e[a]))
      //         ? that.format(e[a])
      //         : e[a];
      //   });
      //   return obj;
      // });

      return data
    }
  },
  methods: {
     visibilityChanged(data){
            // console.log(data)
             this.visible=this.visible+100;
        },
    tes() {
      let data =
        this.datanya[0].judul.toLowerCase().split(' ').join('-') +
        '-1'.split('-')
      console.log(data[data.length - 1])
    },
    pagPrev() {
      this.pembagiannya=10;
      this.pagination = this.pagination - 1
      if (this.pagination == 0) {
        this.pagination = 1
      }
      this.temp2 = this.pagination * this.pembagiannya
      this.temp1 = Math.ceil(this.temp2) - this.pembagiannya
    },
    pagNext() {
      this.pembagiannya=10;
      this.pagination = this.pagination + 1
      if (this.pagination > this.jumlahPagination + 1) {
        this.pagination = Math.ceil(this.jumlahPagination)
      }
      this.temp2 = this.pagination * this.pembagiannya
      this.temp1 = Math.ceil(this.temp2) - this.pembagiannya
    },
    changePagination(pag) {
      this.pembagiannya=10;
      this.pagination = pag
      this.temp2 = this.pagination * this.pembagiannya
      this.temp2 = Math.ceil(this.temp2)
      this.temp1 = this.temp2 - this.pembagiannya
      this.temp1 = Math.ceil(this.temp1)
    },
  },
  mounted () {
       sdb
          .collection('app_portofolio_project',false,urlnya)
          .doc()
          .select(`select * from app_portofolio_project where iduser='${this.$store.state.id}'`)
          .then((res) => {
            console.log(res)
            let datas = res;
            datas=datas.map(e=>{
                return {
                  ...e,
                  urutan:parseInt(e.urutan)
                }
              })
          datas = _.orderBy(datas, 'urutan', 'asc');
            this.datanya = datas
            this.$forceUpdate()
          })
          
          sdb.collection("app_portofolio_project_kategori",false,urlnya).doc().select(`select * from app_portofolio_project_kategori`).then(res => {
